:root {
    --dark-color: #19283f;
    --green-color : #33d1cc;
    --red-color : #ff3150;
    --yellow-color : #ffc400;
    --section-color : #eff7fa;
}

body {
    font-family: 'Roboto', sans-serif;
}

.main-btn {
    background-color: var(--red-color);
    color: var(--yellow-color);
    padding: 0.5rem 1rem;
}

.main-btn:hover {
    color: var(--yellow-color);
}

.main-title::after {
    content: '';
    width: 120px;
    height: 2px;
    background-color: var(--green-color);
    position: absolute;
    bottom: -20px;
    left: 50%;
    transform: translateX(-50%);
}

/* Start Navbar */

.navbar {
    background-color: var(--dark-color);
}

.navbar .navbar-nav .nav-link {
    color: #fff;
}

.navbar .navbar-nav .nav-link.active,
.navbar .navbar-nav .nav-link:focus,
.navbar .navbar-nav .nav-link:hover {
    color: var(--green-color);
}

.navbar .search {
    border-left: 1px solid var(--green-color);
}

.navbar .search svg {
    color: var(--green-color);
}

.navbar .navbar-toggler {
    color: #fff;
    font-size: 25px;
    border-color: #fff;
}

.navbar .navbar-toggler:focus {
    box-shadow: none;
}

/* End Navnbar */

/* Start Landing */

.Landing {
    background-color: var(--dark-color);
    min-height: calc(100vh - 72px);
}
/* End Landing */

/* Start Featuers */
.featuers .icon-holder {
    height: 200px;
}

.featuers .icon-holder svg {
    left: 50%;
    transform: translateX(-50%);
}

.featuers .icon-holder .number {
    font-size: 12rem;
    color: var(--section-color);
}

.featuers .icon-holder .icon {
    color: var(--green-color);

}

.featuers .feat h4 {
    color: var(--yellow-color);
}
/* End Featuers */

/* Start Our Work */
.our-work {
    background-color: var(--section-color);
}

.our-work ul .active {
    background-color: var(--red-color);
    color: var(--yellow-color);
}

.our-work ul li {
    padding: 0.5rem 1rem;
    cursor: pointer;
}

.our-work ul li:not(.active):hover {
    color: var(--red-color);
}

.our-work .box {
    padding: 5px;
    overflow: hidden;
    position: relative;
}

.our-work .box::before {
    content: attr(data-work);
    position: absolute;
    background-color: rgb(51 209 204 / 76%);
    width: calc(100% - 10px);
    height: calc(100% - 10px);
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    color: #fff;
    transition: 0.3s;
    font-size: 1.5rem;
    transform: translateX(calc(-100% - 5px));
}

.our-work .box:hover::before {
    transform: translateX(0);
}
/* End Our Work */

/* Start Stuff */
    .staff .discription {
        max-width: 500px;
    }
/* End Stuff */

/* Start Team */
.team {
    background-color: var(--section-color);
}

.team h2 {
    color: var(--yellow-color);
}

.team .box h4 {
    background-color: var(--green-color);
}
/* End Team */

/* Start Project */
.project {
    background-color: var(--dark-color);
}
/* End Project */

/* Start Sup */
.sup {
    background-color: var(--yellow-color);
}

.sup input[type="text"] {
    border: none;
    border-bottom: 1px solid #fff;
}

.sup input[type="text"]:focus {
    outline: none;
}

.sup ::placeholder {
    color: #FFF;
}

.sup input[type="submit"] {
    background-color: var(--dark-color);
    color: var(--yellow-color);
}
/* End Sup */

/* Start Footer */
.footer {
    background-color: var(--dark-color);
}

.footer .copyright > span {
    color: var(--green-color);
}

.footer .copyright div span {
    color: var(--yellow-color);
}

.footer .contact ul svg {
    width: 20px;
    height: 20px;
}

.footer .facebook {
    background-color: #1877f2;
}

.footer .twitter {
    background-color: #1da1f2;
}

.footer .linkedin {
    background-color: #0077b5;
}

.footer .youtube {
    background-color: #ff0000;
}
/* End Footer */